// Enhanced Session Management Service
import { SimpleAuditLogger } from "./simpleAuditLogger"
import { SessionStore, type SessionInfo } from "./sessionStore"

export interface SessionConfig {
  sessionTimeout: number // in minutes
  maxConcurrentSessions: number
  refreshTokenRotation: boolean
  requireHTTPS: boolean
  sessionFingerprinting: boolean
}

export class SessionManager {
  private static config: SessionConfig = {
    sessionTimeout: 30, // 30 minutes
    maxConcurrentSessions: 3,
    refreshTokenRotation: true,
    requireHTTPS: true,
    sessionFingerprinting: true,
  }

  /**
   * Create a new session
   */
  static async createSession(
     userId: string,
     sessionToken: string,
     refreshToken?: string,
     deviceInfo?: string,
     ipAddress?: string,
     userAgent?: string
   ): Promise<SessionInfo> {
     // Create session using SessionStore
     const sessionInfo = await SessionStore.createSession(
       userId,
       sessionToken,
       refreshToken,
       deviceInfo,
       ipAddress,
       userAgent
     )

     // Log session creation
     await SimpleAuditLogger.logSecurityEvent('PASSWORD_CHANGE', userId, {
       action: 'session_created',
       sessionId: sessionInfo.id,
       deviceInfo,
       timestamp: new Date().toISOString()
     }, ipAddress, userAgent)

     return sessionInfo
   }

  /**
   * Validate and refresh session
   */
  static async validateSession(sessionToken: string, ipAddress?: string, userAgent?: string): Promise<{
     valid: boolean
     session?: SessionInfo
     reason?: string
   }> {
     // Use SessionStore to validate session
     return await SessionStore.validateSession(sessionToken, ipAddress, userAgent)
   }

  /**
   * Refresh session token
   */
  static async refreshSession(
     refreshToken: string,
     ipAddress?: string,
     userAgent?: string
   ): Promise<{
     success: boolean
     newSessionToken?: string
     newRefreshToken?: string
     reason?: string
   }> {
     try {
       if (this.config.refreshTokenRotation) {
         // Generate new tokens
         const newSessionToken = this.generateSecureToken(32)
         const newRefreshToken = this.generateSecureToken(32)

         await SimpleAuditLogger.logSecurityEvent('PASSWORD_CHANGE', 'system', {
           action: 'session_refreshed',
           oldRefreshToken: refreshToken.substring(0, 8) + '...',
           newRefreshToken: newRefreshToken.substring(0, 8) + '...',
           timestamp: new Date().toISOString()
         }, ipAddress, userAgent)

         return {
           success: true,
           newSessionToken,
           newRefreshToken
         }
       }

       return { success: false, reason: 'Token rotation disabled' }
     } catch (error) {
       console.error('Error refreshing session:', error)
       return { success: false, reason: 'Session refresh failed' }
     }
   }

  /**
   * Invalidate a session
   */
  static async invalidateSession(
     sessionToken: string,
     userId?: string,
     reason: string = 'manual_invalidation',
     ipAddress?: string,
     userAgent?: string
   ): Promise<{ success: boolean }> {
     // Use SessionStore to invalidate session
     const success = await SessionStore.invalidateSession(sessionToken)

     if (success) {
       await SimpleAuditLogger.logSecurityEvent('PASSWORD_CHANGE', userId || 'system', {
         action: 'session_invalidated',
         reason,
         sessionToken: sessionToken.substring(0, 8) + '...',
         timestamp: new Date().toISOString()
       }, ipAddress, userAgent)
     }

     return { success }
   }

  /**
   * Invalidate all sessions for a user
   */
  static async invalidateAllUserSessions(
     userId: string,
     reason: string = 'security_reason',
     ipAddress?: string,
     userAgent?: string
   ): Promise<{ success: boolean; invalidatedCount: number }> {
     // Use SessionStore to invalidate all user sessions
     const invalidatedCount = await SessionStore.invalidateAllUserSessions(userId)

     await SimpleAuditLogger.logSecurityEvent('PASSWORD_CHANGE', userId, {
       action: 'all_sessions_invalidated',
       reason,
       invalidatedCount,
       timestamp: new Date().toISOString()
     }, ipAddress, userAgent)

     return { success: true, invalidatedCount }
   }

  /**
   * Get all active sessions for a user
   */
  static async getUserSessions(userId: string): Promise<SessionInfo[]> {
     // Use SessionStore to get user sessions
     return await SessionStore.getUserSessions(userId)
   }

  /**
   * Clean up expired sessions
   */
  static async cleanupExpiredSessions(): Promise<number> {
     // Use SessionStore to clean up expired sessions
     return await SessionStore.cleanupExpiredSessions()
   }

  /**
   * Check for suspicious activity
   */
  static async checkSuspiciousActivity(
     userId: string,
     currentIpAddress: string,
     currentUserAgent: string
   ): Promise<{
     suspicious: boolean
     reasons: string[]
   }> {
     const reasons: string[] = []
     let suspicious = false

     try {
       const sessions = await SessionStore.getUserSessions(userId)
       
       // Check for IP address changes
       const ipAddresses = [...new Set(sessions.map(s => s.ipAddress).filter(Boolean))]
       if (ipAddresses.length > 1 && !ipAddresses.includes(currentIpAddress)) {
         reasons.push('Multiple IP addresses detected')
         suspicious = true
       }

       // Check for new device/user agent
       const userAgents = [...new Set(sessions.map(s => s.userAgent).filter(Boolean))]
       if (userAgents.length > 1 && !userAgents.includes(currentUserAgent)) {
         reasons.push('Multiple devices detected')
         suspicious = true
       }

       // Check for rapid session creation
       const recentSessions = sessions.filter(s => 
         Date.now() - s.createdAt.getTime() < 5 * 60 * 1000 // Last 5 minutes
       )
       if (recentSessions.length > this.config.maxConcurrentSessions) {
         reasons.push('Rapid session creation detected')
         suspicious = true
       }

       if (suspicious) {
         await SimpleAuditLogger.logSecurityEvent('UNAUTHORIZED_ACCESS', userId, {
           action: 'suspicious_activity',
           reasons,
           currentIpAddress,
           currentUserAgent
         }, currentIpAddress, currentUserAgent)
       }

       return { suspicious, reasons }
     } catch (error) {
       console.error('Error checking suspicious activity:', error)
       return { suspicious: false, reasons: [] }
     }
   }

  /**
   * Generate secure random token
   */
  private static generateSecureToken(length: number): string {
     const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
     let token = ''
     
     for (let i = 0; i < length; i++) {
       token += chars.charAt(Math.floor(Math.random() * chars.length))
     }
     
     return token
   }

  /**
   * Get session statistics
   */
  static async getSessionStats(): Promise<{
     totalSessions: number
     activeSessions: number
     expiredSessions: number
     averageSessionDuration: number
   }> {
     // Use SessionStore to get session statistics
     return await SessionStore.getSessionStats()
   }

  /**
   * Update configuration
   */
  static updateConfig(newConfig: Partial<SessionConfig>) {
     this.config = { ...this.config, ...newConfig }
   }

  /**
   * Get current configuration
   */
  static getConfig(): SessionConfig {
     return { ...this.config }
   }
}