import { prisma } from "@/lib/prisma"

export interface SessionInfo {
  id: string
  userId: string
  sessionToken: string
  refreshToken: string | undefined
  deviceInfo: string | undefined
  ipAddress: string | undefined
  userAgent: string | undefined
  createdAt: Date
  lastActivity: Date
  expiresAt: Date
  status: 'active' | 'expired' | 'revoked'
}

export class SessionStore {
  /**
   * Create a new session in the database
   */
  static async createSession(
     userId: string,
     sessionToken: string,
     refreshToken?: string,
     deviceInfo?: string,
     ipAddress?: string,
     userAgent?: string
   ): Promise<SessionInfo> {
     const expiresAt = new Date(Date.now() + 30 * 60 * 1000) // 30 minutes

     const sessionData: SessionInfo = {
       id: `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
       userId,
       sessionToken,
       refreshToken: refreshToken || undefined,
       deviceInfo: deviceInfo || undefined,
       ipAddress: ipAddress || undefined,
       userAgent: userAgent || undefined,
       createdAt: new Date(),
       lastActivity: new Date(),
       expiresAt,
       status: 'active',
     }

     // Store session in database
     await prisma.userSession.create({
       data: {
         id: sessionData.id,
         userId: sessionData.userId,
         sessionToken: sessionData.sessionToken,
         refreshToken: sessionData.refreshToken || null,
         deviceInfo: sessionData.deviceInfo || null,
         ipAddress: sessionData.ipAddress || null,
         userAgent: sessionData.userAgent || null,
         createdAt: sessionData.createdAt,
         lastActivity: sessionData.lastActivity,
         expiresAt: sessionData.expiresAt,
         status: sessionData.status.toUpperCase() as 'ACTIVE' | 'EXPIRED' | 'REVOKED',
       },
     })

     return sessionData
   }

   /**
   * Get session by token
   */
   static async getSession(sessionToken: string): Promise<SessionInfo | null> {
     try {
       const session = await prisma.userSession.findUnique({
         where: { sessionToken },
       })

       if (!session) {
         return null
       }

       // Check if session has expired
       if (session.expiresAt < new Date()) {
         // Mark as expired in database
         await prisma.userSession.update({
           where: { id: session.id },
           data: { status: 'EXPIRED' as const },
         })
         return null
       }

       // Check if session is revoked
       if (session.status === 'REVOKED' || session.status === 'EXPIRED') {
         return null
       }

       return {
         id: session.id,
         userId: session.userId,
         sessionToken: session.sessionToken,
         refreshToken: session.refreshToken || undefined,
         deviceInfo: session.deviceInfo || undefined,
         ipAddress: session.ipAddress || undefined,
         userAgent: session.userAgent || undefined,
         createdAt: session.createdAt,
         lastActivity: session.lastActivity,
         expiresAt: session.expiresAt,
         status: session.status.toLowerCase() as 'active' | 'expired' | 'revoked',
       }
     } catch (error) {
       console.error('Error getting session:', error)
       return null
     }
   }

   /**
   * Validate session and return session info with validation status
   */
   static async validateSession(sessionToken: string, ipAddress?: string, userAgent?: string): Promise<{
     valid: boolean
     session?: SessionInfo
     reason?: string
   }> {
     try {
       const session = await this.getSession(sessionToken)
       
       if (!session) {
         return { valid: false, reason: 'Session not found or expired' }
       }
       
       // Update last activity if valid
       await this.updateActivity(sessionToken)
       
       return { valid: true, session }
     } catch (error) {
       console.error('Error validating session:', error)
       return { valid: false, reason: 'Session validation failed' }
     }
   }

   /**
   * Update session activity
   */
   static async updateActivity(sessionToken: string): Promise<void> {
     try {
       await prisma.userSession.update({
         where: { sessionToken },
         data: { lastActivity: new Date() },
       })
     } catch (error) {
       console.error('Error updating session activity:', error)
     }
   }

   /**
   * Invalidate a session
   */
   static async invalidateSession(sessionToken: string): Promise<boolean> {
     try {
       const result = await prisma.userSession.updateMany({
         where: { sessionToken },
         data: { status: 'REVOKED' as const },
       })

       return result.count > 0
     } catch (error) {
       console.error('Error invalidating session:', error)
       return false
     }
   }

   /**
   * Invalidate all sessions for a user
   */
   static async invalidateAllUserSessions(userId: string): Promise<number> {
     try {
       const result = await prisma.userSession.updateMany({
         where: { 
           userId,
           status: 'ACTIVE' as const,
         },
         data: { status: 'REVOKED' as const },
       })

       return result.count
     } catch (error) {
       console.error('Error invalidating all user sessions:', error)
       return 0
     }
   }

   /**
   * Get all active sessions for a user
   */
   static async getUserSessions(userId: string): Promise<SessionInfo[]> {
     try {
       const sessions = await prisma.userSession.findMany({
         where: { 
           userId,
           status: 'ACTIVE' as const,
         },
         orderBy: { lastActivity: 'desc' as const },
       })

       return sessions.map(session => ({
         id: session.id,
         userId: session.userId,
         sessionToken: session.sessionToken,
         refreshToken: session.refreshToken || undefined,
         deviceInfo: session.deviceInfo || undefined,
         ipAddress: session.ipAddress || undefined,
         userAgent: session.userAgent || undefined,
         createdAt: session.createdAt,
         lastActivity: session.lastActivity,
         expiresAt: session.expiresAt,
         status: session.status.toLowerCase() as 'active' | 'expired' | 'revoked',
       }))
     } catch (error) {
       console.error('Error getting user sessions:', error)
       return []
     }
   }

   /**
   * Clean up expired sessions
   */
   static async cleanupExpiredSessions(): Promise<number> {
     try {
       const result = await prisma.userSession.deleteMany({
         where: {
           OR: [
             { expiresAt: { lt: new Date() } },
             { status: 'EXPIRED' as const },
           ],
         },
       })

       return result.count
     } catch (error) {
       console.error('Error cleaning up expired sessions:', error)
       return 0
     }
   }

   /**
   * Get session statistics
   */
   static async getSessionStats(): Promise<{
     totalSessions: number
     activeSessions: number
     expiredSessions: number
     averageSessionDuration: number
   }> {
     try {
       const now = new Date()
       
       const [totalSessions, activeSessions, expiredSessions] = await Promise.all([
         prisma.userSession.count(),
         prisma.userSession.count({
           where: {
             status: 'ACTIVE' as const,
             expiresAt: { gt: now },
           },
         }),
         prisma.userSession.count({
           where: {
             OR: [
               { expiresAt: { lt: now } },
               { status: 'EXPIRED' as const },
             ],
           },
         }),
       ])

       // Calculate average duration for active sessions
       const activeSessionsData = await prisma.userSession.findMany({
         where: {
           status: 'ACTIVE' as const,
         },
         select: {
           createdAt: true,
         },
       })

       const averageSessionDuration = activeSessionsData.length > 0
         ? activeSessionsData.reduce((sum, session) => {
             return sum + (now.getTime() - session.createdAt.getTime())
           }, 0) / activeSessionsData.length
         : 0
       
       return {
         totalSessions: totalSessions,
         activeSessions,
         expiredSessions,
         averageSessionDuration: Math.round(averageSessionDuration / 1000 / 60), // Convert to minutes
       }
     } catch (error) {
       console.error('Error getting session stats:', error)
       return {
         totalSessions: 0,
         activeSessions: 0,
         expiredSessions: 0,
         averageSessionDuration: 0,
       }
     }
   }
}